.contact-img-div {
    width: 45%;
    display: flex;
    align-items: bottom;
}

.contact-text {
    width: 50%;
    display: flex;
    flex-direction: column;
    text-align: left;
    padding-top: 15%;
    padding-left: 70px;
    overflow: hidden;
}

.contact-text p {
    font-size: 20px;
    margin-right: 200px;
}

#self-contact-img {
    width: 750px;
}

.contact-btns {
    display: flex;
    justify-content: space-around;
    margin-right: 350px;
}

.contact-btns Button i{
    font-size: 18px;
}

#email-btn {
    border: 1px solid #5ABFC1;
    color: #5ABFC1;
    border-radius: 30px;
}

#social-btn {
    margin-left: 2px;
    border: 1px solid #5ABFC1;
    color: #5ABFC1;
    border-radius: 60px;
}