@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-color {
background: #5ABFC1;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #5ABFC1, #5ABFC1, #5ABFC1); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.main-bg {
  background: #5ABFC1;
}

Link {
  text-decoration: none;
}

.landing-grid {
background: rgba(255,255,255, 0.8);  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, rgba(255,255,255, 0.8), rgba(255,255,255, 0.8), rgba(255,255,255, 0.8)); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;
position: fixed;
top: 12%;
left: 10%;
width: 75%;
height: 75%;
overflow: auto;
}
.img-content {
  position: relative;
  width: 97%;
  height: 100%;
  top: 8%;
}
.avatar-img {
  position: relative;
  top: 5%;
  height: 500px;
}


.banner-text {
  position: relative;
  width: 97%;
  height: 100%;
  top: 25%;
}

.banner-text hr{
  width: 50%;
  margin: auto;
  color: black;
}
.social-links {
  padding-top: 15px;
  display: flex;
  /*justify-content: space-between;*/
  width: 70%;
  margin: auto;
  justify-content: space-evenly;
}
.social-links i {
  color: black;
  font-size: 3em;
  padding: 8px;
}

.img-credit {
  position: relative;
  top: 10%;
  font-size: 12px;
}

.img-credit a {
  text-decoration: none;
  color: #5ABFC1; 
}

.project-cell {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  background-color: white;
}

.project-img img {
  height: 80px;
  border-radius: 50%;
  position: relative;
  top: 10px;
  left: 10px;
}

.project-desc {
  position: relative;
  left: 12px;
  padding-right: 10px;
}

.project-desc i {
  color: black;
  font-size: 3em;
  padding-bottom: 10px;
  position: relative;
  top: -20px;
}

.project-desc p {
  text-align: center;
  position: relative;
  top: -20px;
  padding-left: 20px;
  padding-right: 20px;
}

#github-btn {
  background-color: black;
  position: relative;
  top: -25px;
}
::-webkit-scrollbar {
  display: none;
}

/* Mobile Devices, hide the image */
@media (max-width:629px) {
  ::-webkit-scrollbar {
    display: none;
  }
  img#optionalstuff {
    display: none;
  }
  .img-credit {
    display: none;
  }
  .project-cell {
    width: 100%;
  }
}

/* Tablets, change the location of image*/
/* Portrait */
@media only screen 
  and (min-width: 1024px) 
  and (max-height: 1366px) 
  and (orientation: portrait) 
  and (-webkit-min-device-pixel-ratio: 1.5) {
    img#optionalstuff {
      top: 15%;
      right: 22%;
    }
}

/* Landscape */
@media only screen 
  and (min-width: 1024px) 
  and (max-height: 1366px) 
  and (orientation: landscape) 
  and (-webkit-min-device-pixel-ratio: 1.5) {
    img#optionalstuff {
      top: 10%;
      right: 0%;
    }

}


.landing-grid {
    display: flex;
    overflow: auto;
    left: 13%;
}

.about-text {
    width: 70%;
    display: flex;
    align-items: center;
}

.about-text p {
    font-family: 'Times New Roman', Times, serif;
    font-size: 24px;
    text-align: justify;
    padding-left: 60px;
    padding-right: 60px;
}

.about-text h4 {
    font-family: 'Times New Roman', Times, serif;
    font-size: 24px;
    text-align: justify;
    padding-left: 60px;
    padding-right: 60px;
}

.about-text p a {
    text-decoration: none;
}
.about-img {
    padding-left: 60px;
    width: 30%;
    display: flex;
    align-items: center;
}

#computer-img {
    width: 400px; 
    height: 400px;
    border-radius: 50%;
}

@media (max-width: 767px) {
    .landing-grid {
        flex-direction: column;
        align-items: center;
        overflow: scroll;
        left: 0%;
        width: 100%;
    }

    .about-img {
        width: auto;
        padding-left: 0px;
        padding-top: 10px;
    }

    #computer-img {
        width: 200px;
        height: 200px;
        padding-left: 0px;
    }

    .about-text p {
        text-align: justify;
        padding-top: 20px;
        padding-left: 0px;
        padding-right: 0px;
        font-size: 20px;
        line-height: 20px;
    }
    
    .about-text h4 {
        text-align: center;
        font-size: 25px;
    }
}
.contact-img-div {
    width: 45%;
    display: flex;
    align-items: bottom;
}

.contact-text {
    width: 50%;
    display: flex;
    flex-direction: column;
    text-align: left;
    padding-top: 15%;
    padding-left: 70px;
    overflow: hidden;
}

.contact-text p {
    font-size: 20px;
    margin-right: 200px;
}

#self-contact-img {
    width: 750px;
}

.contact-btns {
    display: flex;
    justify-content: space-around;
    margin-right: 350px;
}

.contact-btns Button i{
    font-size: 18px;
}

#email-btn {
    border: 1px solid #5ABFC1;
    color: #5ABFC1;
    border-radius: 30px;
}

#social-btn {
    margin-left: 2px;
    border: 1px solid #5ABFC1;
    color: #5ABFC1;
    border-radius: 60px;
}
.resume-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.resume-main {
    padding-top: 20px;
    display: flex;
    align-items: center;
}

#resume-frame {
    width: 1000px;
    height: 90vh;
    zoom: 0.75;
}

@media (max-width: 767px) {
    #resume-frame {
        width: 100%;
    }

    .resume-main {
        position: relative;
        top: 30px;
        width: 100%;
        height: 50%;
    }
    .resume-container {
        width: 100%;
        position: relative;
        top: 30px;
        overflow: hidden;
    }
}
